<template>
	<div id="client" :style="{backgroundImage:'url('+bg+')'}">
    <!--用户信息-->
    <user></user>
    <!--客服人物-->
    <div class="serverImg">
    <img src="./../assets/images/game8.png" alt="">
    <img src="./../assets/images/game20.png" alt="" class="codeImg">
    </div>
    <!-- 底部导航菜单 -->
    <tabs :selected="selected"></tabs>
  </div>
</template>

<script>
import tabs from './../components/tabs'
import user from './../components/user'
export default {
  name: 'client',
  data () {
    return {
      selected: '', // 导航当前页的状态
      bg: require('./../assets/images/game1.png'), // 背景图片
    }
  },
  components: {
    tabs, user
  }
}
</script>

<style scoped>
  #client{background-size: 100% 100%;height: 100%;position: fixed;width: 100%;}

  /*客服头像样式*/
  .serverImg{position: fixed; left: 1rem; bottom: 1.6rem; width: 13rem;}
  .serverImg img{width: 100%;}
  .serverImg .codeImg{position: absolute;top: -70%;left: 42%;width: 11rem;}
</style>



